.footer {
  display: flex;
  justify-content: flex-end;
  background: #1e1e1e;
  width: 100%;
  padding: 15px 0px;
  font-size: 13px;
  color: #f5f5f5;
  text-align: end;
}

footer .copyright {
  padding: 0 20px;
}
footer .copyright a {
  display: inline-flex;
  text-decoration: none;
  margin: 0 5px;
  color: #f5f5f5;
  cursor: pointer;
  transition: 0.5s;
}
footer .copyright a:hover {
  transform: scale(1.05);
  color: crimson;
}
