.info-table {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  /* padding: 20px;
  margin-left: 20px; */
  /* margin-right: 20px; */
}
.info-table .info-table-container {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  width: 100%;
  padding: 20px;
  overflow-x: scroll;
}

.info-table-container::-webkit-scrollbar {
  width: 20px; /* width of the entire scrollbar */
}

.info-table-container::-webkit-scrollbar-track {
  /* background: gray; */
  border-radius: 10px;
}

.info-table-container::-webkit-scrollbar-thumb {
  background-color: #e7e7e7;
  border-radius: 10px;
  border: 1px solid white;
}

.invoice-table {
  border: 3px solid #3333335b;
  border-radius: 20px;
  padding: 15px;
  width: 100%;
}
