.invoice {
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  margin: 20px;
  padding: 40px 20px;
  border-radius: 20px;
  margin-bottom: 100px;
}

.invoice-wrapper {
  overflow-x: auto;
}

.header-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  /* margin-bottom: 20px; */
}
.info-header {
  width: 100%;
  text-align: start;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
}
.header-title i {
  cursor: pointer;
  transition: 0.7s;
  margin-right: 20px;
  padding: 10px;
}

.header-title i:hover {
  color: crimson;
}
