.get-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  padding: 40px 20px;
  border-radius: 20px;
  /* box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
    rgba(0, 0, 0, 0.06) 0px 1px 2px 0px; */
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.get-info input {
  min-height: 20px;
  color: #1e1e1e !important;
}
.get-info input::placeholder {
  color: lightgray !important;
}

.get-info .get-info-form {
  width: 100%;
  /* padding: 20px; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin-top: 20px;
}

.get-info .get-info-form > * {
  padding: 11px 8px;
  border: 1px solid rgba(128, 128, 128, 0.288);
  border-radius: 7px;
  outline: none;
  background: transparent;
  font-size: 12px;
  font-family: Poppins;
  color: gray;
  transition: 0.5s;
}

.get-info .get-info-form > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-info .get-info-form > *:focus {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-info .drop-down-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  position: relative;
}

.get-info .drop-down-form .dropdown-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.get-info .drop-down-form .dropdown-header input {
  flex-grow: 1;
  border: none;
  outline: none;
  user-select: none;
  font-size: 12px;
}

.get-info .drop-down-form .dropdown-header i {
  margin-right: 10px;
}
.get-info .drop-down-form .dropdown-content {
  width: 100%;
}

.get-info .form-button {
  grid-column: 1 / -1;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  padding: 0;
  border: none;
  height: 40px;
}

.get-info .get-info-form .form-button button {
  border: none;
  color: #f5f5f5;
  font-size: 14px;
  cursor: pointer;
  transition: 0.5s;
  border-radius: 10px;
  text-transform: uppercase;
  font-family: Poppins;
}

.get-info .get-info-form button:hover {
  opacity: 60%;
}
.get-info .get-info-form .button-reset {
  background-color: crimson;
}

.get-info .get-info-form .button-submit {
  background-color: green;
}

.header-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;

  /* margin-bottom: 20px; */
}

.header-title .info-header {
  width: 100%;
  text-align: start;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
}

.header-title i {
  cursor: pointer;
  transition: 0.7s;
  margin-right: 20px;
  padding: 10px;
}

.header-title i:hover {
  color: crimson;
}

.get-info .get-info-form .form-size {
  display: flex;
  align-items: center;
  border: none;
  padding: 0;
  font-size: 20px;
}

.get-info .get-info-form .form-size input {
  width: 30%;
  margin-right: 5px;
  padding: 11px 8px;
  border-radius: 7px;
  outline: none;
  background: transparent;
  border: 1px solid rgba(128, 128, 128, 0.288);
  font-size: 12px;
  font-family: Poppins;
  color: gray;
  transition: 0.5s;
}

.get-info .get-info-form .form-size input:first-child {
  width: 100%;
}
.get-info .get-info-form .form-size input:last-child {
  margin-left: 5px;
}

@media (max-width: 768px) {
  .get-info .get-info-form {
    grid-template-columns: repeat(1, 1fr) !important;
  }
}
