.table-heading-parent {
  width: 100%;
  min-width: fit-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  background: #333333e5;
  color: #f5f5f5;
  border-radius: 10px;
  flex-wrap: nowrap;
  margin-bottom: 2px;
  height: 45px;
}

.table-heading-parent .table-heading {
  width: 100%;
  border-left: 1px solid #1e1e1e23;
  padding: 10px;
  text-align: center;

  /* flex-grow: 1; */
}
.table-heading-parent .table-heading:first-child {
  border-left: none;
}

.table-heading-parent .row {
  min-width: 15px;
  max-width: 15px;
}

.table-heading-parent .quantity,
.table-heading-parent .width,
.table-heading-parent .length {
  /* flex-grow: 1; */
  min-width: 20px;
}

.table-heading-parent .area,
.table-heading-parent .type,
.table-heading-parent .comment {
  /* flex-grow: 5; */
  min-width: 120px;
}

.table-heading-parent .placement,
.table-heading-parent .color,
.table-heading-parent .Manual,
.table-heading-parent .Motorized,
.table-heading-parent .position,
.table-heading-parent .cassette-type,
.table-heading-parent .cassette-color,
.table-heading-parent .layout,
.table-heading-parent .manufacture,
.table-heading-parent .unit-price,
.table-heading-parent .total-price {
  /* flex-grow: 2; */
  min-width: 55px;
}
