.menu {
  display: flex;
  width: 100%;
  padding: 0;
}
.menu .menu-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  padding: 25px 20px;
}
.menu .menu-container .menu-button {
  padding: 8px 15px;
  border: 1px solid crimson;
  outline: none;
  margin: 0 5px;
  cursor: pointer;
  border-radius: 10px;
  background: transparent;
  color: crimson;
  transition: 0.5s;
  font-size: 11px;
  /* box-shadow: rgba(220, 20, 60, 0.25) 0px 2px 5px -1px,
    rgba(220, 20, 60, 0.3) 0px 1px 3px -1px; */
}
.menu .menu-button:hover {
  color: #f5f5f5;
  background: crimson;
}
.menu .menu-button:last-child {
  background: crimson;
  color: #f5f5f5;
}
.menu .menu-container .menu-button i {
  margin-right: 5px;
}
