.customer-information {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.customer-information .information {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.customer-information .information .info-left,
.customer-information .information .info-right {
  display: flex;
  flex-direction: column;
}

.invoice-header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  border-bottom: 2px solid gray;
  padding-bottom: 20px;
}

.invoice-header img {
  width: 120px;
}
