body {
  width: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-family: "Poppins", sans-serif;
  color: #1e1e1e;
}

#root {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
}

body ul,
body li {
  list-style: none;
  margin: 0;
  padding: 0;
}
