.dropdown-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 12px;
  max-height: 250px;
  overflow-y: scroll;
  position: absolute;
  top: 35px;
  left: 0;
  background: #f5f5f5;
  z-index: 100;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.dropdown-wrapper .dropdown {
  width: 100%;
}

.dropdown-wrapper .dropdown ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.dropdown-wrapper .dropdown ul li {
  width: 100%;
  display: flex;
  justify-content: space-between;
  /* padding: 10px; */
  cursor: pointer;
}
.dropdown-wrapper .dropdown ul li:nth-child(odd) {
  background: lightgray;
  /* border-radius: 5px; */
}

.dropdown-wrapper .dropdown ul li label {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  /* height: 25px; */
  padding: 5px;
  padding-right: 15px;
}
.dropdown-wrapper .dropdown ul li label input {
  display: flex;
  justify-self: flex-end;
  height: 15px;
  width: 15px;
  padding: 10px;
}
